@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
/* css for search-nav */

.nav-search {
  background-color: white;
}
.searched-item {
  text-transform: capitalize;
  /* color: teal; */
  font-weight: 500;
}
.searched-item h6 {
  font-size: 15px;
}

.cart-basket {
  font-size: 0.6rem;
  position: absolute;
  top: 11px;
  right: 21.15%;
  width: 15px;
  height: 15px;
  color: #fff;
  background-color: #418deb;
  border-radius: 50%;
}
.cart-basket-icon {
  top: 18px;
  position: absolute;
  right: 21.8%;
}

.hide-price {
  display: none;
}
@media only screen and (max-width: 768px) {
  .cart-basket,
  .cart-basket-icon {
    top: 45%;
    right: 10%;
  }
  .cart-basket-icon {
    font-size: 25px;
  }
}

/* css for footer-website */
.span-or {
  display: block;
  position: absolute;
  left: 50%;
  top: -2px;
  margin-left: -25px;
  background-color: #fff;
  width: 50px;
  text-align: center;
}
.hr-or {
  background-color: #cdcdcd;
  height: 1px;
  margin-top: 0px !important;
  margin-bottom: 0px !important;
}
footer.nb-footer {
  background: #222;
  border-top: 4px solid #b78c33;
}
footer.nb-footer .about {
  margin: 0 auto;
  margin-top: 40px;
  max-width: 1170px;
  text-align: center;
}
footer.nb-footer .about p {
  font-size: 13px;
  color: #999;
  margin-top: 30px;
}
footer.nb-footer .about .social-media {
  margin-top: 15px;
}

/* footer.nb-footer .about .social-media ul li {
	float: left;
} */
footer.nb-footer .about .social-media ul li a {
  display: inline-block;
  width: 45px;
  height: 45px;
  line-height: 45px;
  border-radius: 50%;
  font-size: 16px;
  color: #b78c33;
  border: 1px solid rgba(255, 255, 255, 0.3);
}
footer.nb-footer .about .social-media ul li a:hover {
  background: #b78c33;
  color: #fff;
  /* border-color: #b78c33; */
  border-color: deepskyblue;
}
footer.nb-footer .footer-info-single {
  margin-top: 30px;
}
footer.nb-footer .footer-info-single .title {
  color: #aaa;
  text-transform: uppercase;
  font-size: 16px;
  border-left: 4px solid #b78c33;
  padding-left: 5px;
}
footer.nb-footer .footer-info-single ul li a {
  display: block;
  color: #aaa;
  padding: 2px 0;
}
footer.nb-footer .footer-info-single ul li a:hover {
  color: #b78c33;
}
footer.nb-footer .footer-info-single p {
  font-size: 13px;
  line-height: 20px;
  color: #aaa;
}
footer.nb-footer .copyright {
  margin-top: 15px;
  background: #111;
  padding: 7px 0;
  color: #999;
}
footer.nb-footer .copyright p {
  margin: 0;
  padding: 0;
}
.facebook:hover {
	background-color: #3b5998 !important;
}
.twitter:hover {
	background-color: #55acee !important;
}
.linkedin:hover {
	background-color: #0077b5 !important;
}
.youtube:hover {
	background-color: #cd201f !important;
}
.instagram:hover {
	background-color: #e4405f !important;
}




/* css for app */
.page {
  padding: 100px 0px 0px 0px;
}

.main-page {
  min-height: 65vh !important;
}
a {
  text-decoration: none !important;
  color: white !important;
}
a:hover {
  color: hotpink;
}
.title {
  color: rgb(100, 81, 81);
  font-size: 15px !important;
}
.price {
  font-size: 14px;
  color: rgb(255, 111, 0);
  font-family: cursive;
  padding-top: 5px !important;
  float: left;
}
.cart-icon {
  color: orange;
}

.custom-food-image {
  border-radius: 1rem 1rem 0 0;
  width: 500px !important;
}

.food-card .card,
.food-card .card-body {
  border-radius: 1rem;
}

.cursor {
  cursor: pointer;
}
.full-details-arrow {
  font-size: 10px !important;
}

p {
  font-size: 14px;
}

.content {
  padding-top: 140px;
}
.a {
  font-size: 12px !important;
}

.Carousel .card {
  min-height: 320px;
}
.my-custom-scrollbar {
  position: relative;
  height: 330px;
  overflow: auto;
}
.table-wrapper-scroll-y {
  display: block;
}
.details li,
.health-label {
  font-size: 14px;
}
.title-Ingredients {
  font-weight: 700;
  padding: 15px;
}
.modal-header .close {
  padding: 0;
  margin: 0;
}
.modal-header {
  padding: 2px !important;
  margin: 0 2px;
  border: none;
}
.modal-body {
  padding: 0 0 15px 0 !important;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background-color: #ebebeb;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #6d6d6d;
}

.spinner {
  align-items: center !important;
}

.App {
  text-align: center;
  /* padding-top: 50px; */
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.cart .img {
  height: 80px;
  width: 80px;
}
.cart .title {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 20px;
  color: black !important;
}

.my-table {
  position: -webkit-sticky !important;
  position: sticky !important; /* this does the trick */
  top: 55px !important;
  background-color: black;
  transition: 0.1s ease-in-out;
}

.btn:focus {
  outline: none !important;
  box-shadow: none !important;
}

@media only screen and (max-width: 768px) {
  .my-modal .modal-img {
    height: 80vw !important;
  }
  .food-card .card {
    min-height: auto !important;
  }

  /* cart */
  .table th,
  .table td {
    font-size: 10px !important;
    font-weight: 100 !important;
  }
  .cart-img {
    width: 10vw !important;
    height: 10vw !important;
  }
  .cart-amount-icon {
    font-size: 10px !important;
    padding: 1px !important;
    border: 0.1 px !important;
  }
}

/* css for navbar */
body {
  font-family: "Open Sans", sans-serif;
}

.navbar .nav-item {
  padding: 0 10px !important;
}
.navbar .nav-link {
  color: black !important;
}
.navbar {
  background-color: white;
  transition: 0.8s;
}
.logo-before-scroll {
  height: 40px;
  transition: 0.8s;
}
.logo-after-scroll {
  height: 60px;
  transition: 0.8s;
}

